*, *:before, *:after {
	vertical-align: baseline;
	font-weight: inherit;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: 0 none;
	outline: 0 none;
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html,body {
	width: 100%;
	height: 100%;
	font-family: $mainFont;
	font-size: 16px;
	line-height: 1;
	@media (max-width: 960px) {
		font-size: 14px;
	}
}

ul,li {
	list-style-type: none;
}
p {
	margin: 0;
}
a {
	text-decoration: none;
	color: black;
}
img {
	max-width: 100%;
}
audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

textarea {
	resize: none;
}

@include tablets {
	html,body {
		font-size: 14px;
	}
}
@include phones {
	html,body {
		font-size: 12px;
	}
}

@keyframes fadein{
	from{opacity:0;
	transform:translateY(-30px);}
	to{opacity:1;
	transform:translateY(0);}}

@keyframes fadetop{
	from{opacity:0;
	transform:translateY(60px);}
	to{opacity:1;
	transform:translateY(0);}}

@keyframes fadenomove{
	from{opacity:0;}
	to{opacity:1;}
}

@keyframes arrowbounce {
	from {
	transform: translateY(0);
	}
	to {
		transform: translateY(-20px);
	}
}